.user-controller {
  position: absolute;
  height: 200px;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #FFFFFF;
  border-top: 1px solid #f4f6fb;
}
.user-controller .user-controller-get-triage {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-size: 16px;
}
.user-controller .user-controller-footer {
    width: 100%;
    padding: 10px 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: #fff;
    overflow: hidden;
    text-align: right;
}
@media (max-width: 1600px) {
.user-controller .user-controller-footer {
        padding-bottom: 25px;
}
}
.user-controller .user-controller-footer .user-send-message {
      font-size: 15px;
      line-height: 35px;
      color: #ccc;
      margin-right: 20px;
}
.user-controller .user-controller-footer .user-controller-send-btn {
      margin-right: 30px;
}
.user-controller .user-controller-input {
    display: block;
    background-color: #ffffff;
    border: none;
    resize: none;
    width: 100%;
    height: 90px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 4px 10px;
    font-size: 24px;
}
.user-controller .user-controller-fastBtn {
    padding: 10px 40px;
    font-size: 0;
}
@media (max-width: 1440px) {
.user-controller .user-controller-fastBtn {
        padding: 10px 10px;
}
}
.user-controller .user-controller-fastBtn > button {
      cursor: pointer;
      font-size: 16px;
      color: #808080;
      margin-right: 40px;
}
@media (max-width: 1440px) {
.user-controller .user-controller-fastBtn > button {
          margin-right: 10px;
}
}
@media (max-width: 1440px) {
.user-controller .user-controller-fastBtn > button > i {
          margin-right: 0;
}
}
.user-controller .user-controller-fastBtn > button.reply-on {
        color: #6B748C;
}
