}
}
.center-inner-message {
  float: left;
  width: 100%;
  height: 100%;
}
.center-inner-message > .layout-helper {
    margin: 0 385px 0 400px;
    position: relative;
    padding-top: 55px;
    padding-bottom: 40px;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.center-inner-message .center-inner-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 2px;
    background-color: #fff;
    z-index: 3;
    text-align: center;
    display: none;
}
.center-inner-message .center-inner-wrapper:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      height: 100%;
}
.center-inner-message .center-inner-wrapper > img {
      width: 170px;
      height: 170px;
      vertical-align: middle;
}
@media (max-width: 1440px) {
.center-inner-message > .layout-helper {
      margin: 0 385px 0 315px;
}
}
.no-content .layout-helper {
  margin-right: 0;
}
.no-content .medical-record {
  display: none;
}
.no-content .center-inner-wrapper {
  display: block;
}



