  text-align: center;
  cursor: pointer;
}
.centerTip .tip-close {
  width: 16px;
  height: 16px;
  background: url("/static/image/img00/employee/picture_close.png") 50% 50% no-repeat;
  position: absolute;
  top: 0;
  right: -36px;
  cursor: pointer;
}
.tip-save-result {
  width: 120px;
  height: 120px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #000;
  border-radius: 8px;
  opacity: 0.9;
  text-align: center;
  font-size: 16px;
  color: #FFFFFF;
  letter-spacing: 0;
  line-height: 16px;
  z-index: 7;
  -webkit-transition: opacity 0.5s;
}
.tip-save-result img {
    margin-top: 20px;
}
.tip-save-result .text {
    margin-top: 14px;
}
.tip-save-result.on {
    -webkit-animation: fadeOut 2s  linear 0s forwards 1;
}



