
.make-photo-box {
  -webkit-transform: rotate(90deg) translate(12%, 12%);
          transform: rotate(90deg) translate(12%, 12%);
}
.video-list {
  position: relative;
}
.video-list > img {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}



