
.resendTips {
  width: 15px;
  height: 15px;
  background: red;
  color: #fff;
  border-radius: 50%;
  display: inline-block;
  margin-right: 10px;
  text-align: center;
  line-height: 15px;
  cursor: pointer;
}
.messageList-item-name {
  line-height: 35px;
  display: inline-block;
  font-size: 14px;
  height: 30px;
  margin-left: 5px;
  text-align: left;
  background: #00D6C6;
  border-radius: 4px;
  color: #fff;
  padding: 2px 5px;
}
.messageList-item-name p {
    line-height: 15px;
    height: 15px;
    color: #eee;
}
.messageList-item-name p:last-child {
      color: #fff;
}



