        width: 12px;
        height: 12px;
        display: inline-block;
        top: 0;
        margin-right: 4px;
        cursor: pointer;
        vertical-align: baseline;
}
.medical-record-remark-header section .remark-new i {
      background: url("/static/image/img00/employee/home_note_return.png") no-repeat;
}
.medical-record-remark-header section .remark-back {
