                box-sizing: border-box;
        height: 300px;
        line-height: 1.2;
        outline: medium;
}
.medical-record-remark-content .edit textarea {
        padding: 20px;
        -webkit-box-sizing: border-box;
                box-sizing: border-box;
        height: 280px;
        width: 100%;
        line-height: 1.2;
        outline: medium;
}
.medical-record-remark-content .edit .saveButton {
        font-size: 13px;
        letter-spacing: 0;
        line-height: 30px;
        border: 1px solid transparent;
        border-radius: 4px;
        width: 70px;
        height: 30px;
        position: absolute;
        bottom: 20px;
        right: 20px;
        cursor: pointer;
        background: #ECEFF6;
        color: #CCC;
}
.medical-record-remark-content .edit .saveButton.active {
          color: #7A8EC1;
          background: transparent;
          border: 1px solid #7A8EC1;
}
.medical-record-remark-content .active {
      display: block;
}
.medical-record-remark-content .modal-confirm {
      top: 80%;
      right: 14px;
      left: auto;
}
.medical-record-remark .medical-record-remark-content {
    display: none;
}
.medical-record-remark.on {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.medical-record-remark.on .medical-record-remark-content {
      display: block;
}
.remark-content-item {
  padding: 20px 15px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 75px;
  position: relative;
  background: #FAFBFD;
  -webkit-box-shadow: 0 0 8px 0 rgba(179, 205, 199, 0.45);
          box-shadow: 0 0 8px 0 rgba(179, 205, 199, 0.45);
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.remark-content-item > .text {
    font-size: 14px;
    color: #222222;
    letter-spacing: 0;
    line-height: 14px;
    width: 90%;
