    overflow: hidden;
    text-overflow: ellipsis;
}
.remark-content-item > .text:hover {
      text-decoration: underline;
      cursor: pointer;
}
.remark-content-item > .time {
    font-size: 14px;
    color: #909090;
    margin-top: 10px;
    letter-spacing: 0;
    line-height: 12px;
}
.remark-content-item:hover .delete {
    background: url("/static/image/img00/index/home_note_delete.png") no-repeat;
    width: 18px;
    height: 18px;
    display: inline-block;
    position: absolute;
    right: 20px;
    top: 28px;
    cursor: pointer;
}



