
.make-photo-box {
  -webkit-transform: rotate(90deg) translate(12%, 12%);
          transform: rotate(90deg) translate(12%, 12%);
}
.file {
  background-color: rgba(255, 255, 255, 0.8) !important;
  -webkit-box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.16);
  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.16);
  padding: 6px 6px 6px 6px;
  margin-bottom: 4px !important;
  cursor: pointer;
  border-radius: 5px;
}
.file .fileImg {
    display: table-cell;
    vertical-align: middle;
    width: 45px;
    height: 100%;
    text-align: center;
}
.file .fileImg img {
      width: 45px;
      height: 45px;
}
.file .fileInfo {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: table-cell;
    vertical-align: middle;
    max-width: 245px;
    line-height: 1.3;
    padding-left: 5px;
}
.file .fileSize {
    margin-top: 10px;
    text-align: right;
    color: #aaa;
}



