
.sendImg {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
.sendImg input[type='file'] {
    width: 100%;
    height: 100%;
    background: transparent;
    opacity: 0;
    z-index: 2;
    position: relative;
}
.sendImg .btn-click {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    cursor: pointer;
    z-index: 1;
}
.sendImg .btn-click img {
      display: block;
      margin: 0 auto;
      margin-bottom: 5px;
}
.sendImg .btn-click span {
      color: #fff;
      font-size: 20px;
      line-height: 20px;
      display: block;
      text-align: center;
}
.sendImg .imgList {
    position: absolute;
    top: 0;
    left: 0;
    width: auto;
    height: 100%;
    padding-top: 25px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 3;
}
.sendImg .imgList .imgInfo {
      background: rgba(255, 255, 255, 0.8);
      position: relative;
      display: inline-block;
      margin: 17px 5px;
      vertical-align: top;
      border: 1px solid #acacac;
      padding: 6px 6px 6px 6px;
      -webkit-box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.16);
              box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.16);
      font-size: 14px;
}
.sendImg .imgList .imgInfo > img {
        display: block;
        width: 100px;
        height: 100px;
}
.sendImg .imgList .imgInfo .name {
        width: 100px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.sendImg .imgList .imgInfo .size {
        padding-top: 5px;
        font-size: 14px;
        line-height: 14px;
}
.sendImg .imgList .imgInfo .size.on {
          color: red;
}
.sendImg .imgList .imgInfo .size .num {
          font-weight: 600;
}
.sendImg .imgList .imgInfo .remove {
        width: 40px;
        height: 40px;
        background: rgba(255, 255, 255, 0.8);
        border-radius: 100%;
        text-align: center;
        line-height: 35px;
        position: absolute;
        right: -10px;
        top: -10px;
}
.sendImg .imgList .imgInfo .remove > img {
          width: 16px;
          height: 16px;
          padding: 12px;
}
.sendImg .send-btn {
    width: 95px;
    height: 35px;
    background-color: #7a8ec1;
    border-radius: 5px;
    cursor: pointer;
    vertical-align: middle;
    position: absolute;
    right: 30px;
    bottom: 21px;
    z-index: 3;
}
.sendImg .send-btn a {
      width: 100%;
      height: 100%;
      text-align: center;
      line-height: 35px;
      color: #fff;
      display: block;
}
.sendImg .closeSend {
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-top: 40px solid #fff;
    border-left: 40px solid transparent;
    z-index: 3;
}
.sendImg .closeSend:after {
      content: '';
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      /*background: url("../../assets/img00/common/popup_close_activate.png") no-repeat;*/
      background: url("/static/image/img00/common/popup_close_activate.png") no-repeat;
      top: -36px;
      right: 4px;
      cursor: pointer;
}



