  color: #aaaaaa;
  margin-bottom: 10px;
  /*margin-top: 40px;*/
  text-align: center;
}
.fadeDown-enter-active {
  -webkit-transition: all ease-in-out 0.5s;
  transition: all ease-in-out 0.5s;
}
.fadeDown-enter {
  opacity: 0;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}



