.modal-confirm {
  position: absolute;
  width: 250px;
  top: 50%;
  left: 50%;
  background: #FFFFFF;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(153, 167, 208, 0.35);
          box-shadow: 0px 0px 20px 0px rgba(153, 167, 208, 0.35);
  border-radius: 4px;
  padding: 40px 20px;
  padding-bottom: 20px;
  min-height: 155px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 5;
}
.modal-confirm.upShow {
    bottom: 60px;
    top: auto;
}
.modal-confirm.upShow:after {
    top: auto;
    bottom: -10px;
    height: 0;
    width: 0;
    border-top: 5px solid #fff;
    border-bottom: 5px solid transparent;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
}
.modal-confirm:after {
    content: '';
    display: block;
    height: 0;
    width: 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid #fff;
    border-left: 5px solid transparent;
    border-right: 5px solid transparent;
    position: absolute;
    top: -10px;
    right: 10px;
}
.modal-confirm-content p {
    font-size: 13px;
    color: #808080;
    line-height: 1.5;
}
.modal-confirm .modal-confirm-button {
    text-align: center;
    position: absolute;
    bottom: 20px;
    left: 0;
    right: 0;
}
.modal-confirm .modal-confirm-button > button {
      margin: 0 10px;
      width: 58px;
      height: 24px;
      font-size: 12px;
      border-radius: 3px;
      cursor: pointer;
      display: inline-block;
      vertical-align: middle;
}
.modal-confirm .modal-confirm-button .modal-confirm-ensure {
      border: 1px solid #7A8EC1;
      color: #7a8ec1;
}
.middle-tip-box {
  position: absolute;
  width: 100%;
  height: 100%;
  /*top: 50%;*/
  /*left: 50%;*/
  top: 0;
  left: 0;
  z-index: 5;
